import React, { Fragment, useEffect, useState } from "react";
import { Field } from "redux-form";
import RadioInput from "../../../../../../partials/form/RadioInput";

const RenderTable = ({ fields, type, data, title }) => {
  const [column, setColumn] = React.useState([""]);
  const [selectedRow, setSelectedRow] = useState(null);
  useEffect(() => {
    console.log(fields);
    setColumn(Object.keys(data[0]).filter((el) => el !== "id"));
    fields.removeAll();
    data.forEach(function(element) {
      fields.push({ data: element });
    });
  }, []);

  const handleSelectAllToggle = (e) => {
    console.log("calleds");

    const all = fields.getAll();
    const selected = document.getElementById(`${fields.name}-checkToggle`)
      .checked
      ? true
      : false;

    fields.removeAll();
    all.forEach((item, index) => {
      all[index].selected = selected;
      fields.push(item);
    });
  };

  const handleCLick = (data) => {
    console.log("called");
    // setSelectedRow(data);
    // handleRowClick({ type: fields.name, value: data });
  };
  return (
    <Fragment>
      <div
        style={{ height: "30vh" }}
        className={`tableTheme theme-table-striped pb-3 ${type}`}
      >
        <table class="table">
          <thead>
            <tr>
              <th scope="col">
                <input
                  id={`${fields.name}-checkToggle`}
                  type="checkbox"
                  onChange={handleSelectAllToggle}
                />
              </th>
              {column.map((value, index) => (
                <Fragment> {(index === 0) && title ?  <th key={index}>{title}</th> : <th key={index}>{value}</th> }</Fragment>
              ))}
            </tr>
          </thead>
          <tbody>
            {fields.map((item, index) => (
              <tr
                key={index}
                onClick={() => handleCLick(fields.get(index)?.data)}
                className={
                  fields.get(index)?.data === selectedRow ? "row-selected" : ""
                }
              >
                <th scope="row" onClick={(e) => e.stopPropagation()}>
                  <Field
                    name={`${item}.selected`}
                    component={RadioInput}
                    type="checkbox"
                  />
                </th>
                {column.map((value, index) => (
                  <th key={index}>{fields.get(index)?.data[value]}</th>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </Fragment>
  );
};
RenderTable.defaultProps = {};
export default RenderTable;
