import React, { Fragment, useEffect } from "react";
import { Table } from "react-bootstrap";
import { Field } from "redux-form";
import RadioInput from "../../../../../../partials/form/RadioInput";

const RenderTableRadio = (props) => {
  const { data, title, name, type } = props;
  const [column, setColumn] = React.useState([""]);
  useEffect(() => {
    setColumn(Object.keys(data[0]).filter((el) => el !== "id"));
  }, []);

  const handleOnclick = (e) => {
    const id = e.currentTarget.dataset.id;
    props.change(name, id);
  };
  return (
    <Fragment>
       <div
        style={{ height: "30vh" }}
        className={`tableTheme theme-table-striped pb-3 ${type}`}
      >

        <table class="table table-hover" id="job-table">
        <thead>
          <tr>
            {column.map((value, index) => (
              <Fragment>
                {" "}
                {index === 0 && title ? (
                  <th key={index}>{title}</th>
                ) : (
                  <th key={index}>{value}</th>
                )}
              </Fragment>
            ))}
          </tr>
        </thead>
        <tbody>
        {data.map((item, index) => (
              <Fragment>
                <tr key={index} data-id={item.id} onClick={handleOnclick}>
                  {console.log(item)}
                  {column.length > 0
                    ? column.map((value, index) => (
                        <td key={index}>
                          {index === 0 ? (
                            <Field
                              name={name}
                              component={RadioInput}
                              type="radio"
                              value={item.id}
                              label={item[value]}
                            />
                          ) : (
                            item[value]
                          )}
                        </td>
                      ))
                    : ""}
                </tr>
              </Fragment>
            ))}

        </tbody>
      </table>
    
  </div>
      
    </Fragment>
  );
};

export default RenderTableRadio;
